<template>
    <input type="text" class="base-input" :value="value" :name="name" @input="onInput">
</template>
<script>
import '@/utils/i18n.js'
export default {
    name: 'BaseInput',
    model: {
        prop: 'value',
        event: 'input'
    },
    props: ['value', 'name'],
    methods: {
        onInput (e) {
            this.$emit('input', e.target.value)
        }
    }
}
</script>
